import * as React from "react";
import qs from 'qs';
import { Toast, Button } from 'antd-mobile'
import { getCourse, courseConfirm } from '../../api/consumer'
import ConsumerPanl from '../../components/consumer/consumerPanl'
import { getSeatActiveTime } from '../../util'

import './consumer.less'
interface ISprops {
    [propsName: string]: any
}
interface IState {
    courseData: any,
    member_id: any,
    btnDisabled: boolean
}

export default class ConsumerInfo extends React.Component<ISprops, IState>{
    constructor(props: ISprops) {
        super(props);
        this.state = {
            courseData: {},
            member_id: '',
            btnDisabled: false
        }
    }
    componentWillMount() {
        document.title = '上课信息';
        this.getInfoList()
    }
    getInfoList = () => {
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        let params = {
            course_id: data.course_id,
            member_id: data.member_id || ''
        }
        this.setState({
            member_id: data.member_id || ''
        })
        getCourse(params).then((data) => {
            if (!data.status) {
                this.setState({
                    courseData: data.data
                })
            } else {
                Toast.fail(data["message"],2)
            }
        })
    }
    checkAttend = () => {
        this.setState({btnDisabled: true})
        let search = this.props.location.search;
        let data = qs.parse(search.substring(1));
        let params = {
            course_id: data.course_id,
            member_id: data.member_id
        }
        courseConfirm(params).then((res) => {
            this.setState({btnDisabled: false})
            if (!res.status) {
                Toast.info("确认成功")
                this.props.history.go(-1)
            } else {
                Toast.fail(res["message"],2)
            }
        })
    }
    render() {
        const { courseData, member_id, btnDisabled } = this.state;
        console.log(!!member_id)
        const time = getSeatActiveTime(courseData.start_time || '', courseData.end_time || '');
        return (
            <div className="consumer-info">
                <div className="consumer-info-c" style={{ paddingBottom: '1rem' }}>
                    <div className="consumer-title">
                        <h4>{courseData.course_name}</h4>
                        <p>{time}  高顿财务培训</p>
                    </div>
                    <ConsumerPanl title="项目信息">
                        <p>日期：{time}</p>
                        <p>签到时间：{courseData.sign_time}</p>
                        <p>议程时间：{courseData.class_time}</p>
                        <p>地点：{courseData.address} {courseData.classroom}</p>
                        <p>现场工作人员：{courseData.operator} ({courseData.operator_phone})</p>
                    </ConsumerPanl>

                    <ConsumerPanl title="特别提醒">
                        <p dangerouslySetInnerHTML={{__html:courseData.tips}}></p>
                    </ConsumerPanl>

                    <ConsumerPanl title="交通指引">
                        <p dangerouslySetInnerHTML={{__html:courseData.guidelines}}></p>
                    </ConsumerPanl>
                    <ConsumerPanl title="住宿信息参考">
                        <p dangerouslySetInnerHTML={{__html:courseData.hotel}}></p>
                    </ConsumerPanl>
                </div>

                {
                    !!member_id ?
                        <div className="evaluate-btn">
                            <Button className="btn" type="primary" onClick={this.checkAttend} disabled={courseData.is_attend !== 0 || btnDisabled}>{courseData.is_attend === 0 ? "确认上课" : "已确认"}</Button>
                        </div>
                        :
                        <div></div>
                }
            </div>
        )
    }

}
